/* рисуем иконку гамбургера из трех горизонтальных линий */
.hamburger {
  position: relative;
  display: block;
  width: 28px;
  height: 30px;
}
.hamburger1,
.hamburger2,
.hamburger3 {
  position: absolute;
  top: 50%;
  height: 2px;
  width: 28px;
  background: #ffffff;
  transition: all 200ms ease-in-out;
  z-index: 10;
}
.hamburger1 {
  top: calc(50% - 10px);
}
  
.hamburger3 {
  top: calc(50% + 10px);
}
.navbar-inverse .navbar-toggler {
  border-color: rgba(255, 255, 255, 1);
}
/* Добавим анимацию, первую и третью полосу ставим под 45град. среднюю заливаем цветом фона. */
.convert .hamburger1 {
  top: 50%;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.convert .hamburger2 {
  background-color: transparent;
}
.convert .hamburger3 {
  top: 50%;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.navbar-toggler:hover {
  cursor: pointer;
}
.navbar-brand {
  color: #ffffff !important;
}
  .navbar-brand:hover {
    color: white !important;
  }
.arrowDown {
  margin: 80px 0px 30px 0px;
  position: reltive;
}

.arrowDown__border {
  width: 50px;
  height: 50px; /* Для Firefox */ /* Для IE */
  -webkit-transform: rotate(45deg); /* Для Safari, Chrome, iOS */ /* Для Opera */
  transform: rotate(45deg);
  margin: 0 auto;

}

  .arrowDown__arrow1 {
    padding: 10px;
    margin: 12px;
    border-right: 7px solid #F85C37;
    border-bottom: 7px solid #F85C37;
    position: absolute;
    top: -12px;
    left: -12px;
    -webkit-animation: scrl 2s infinite;
    animation: scrl 2s infinite;
    opacity: 0;
    -webkit-animation-delay: .15s;
    animation-delay: .15s;
  }

  .arrowDown__arrow2 {
    margin: 12px;
    border-right: 7px solid #F85C37;
    border-bottom: 7px solid #F85C37;
    position: absolute;
    top: 6px;
    left: 6px;
    padding: 10px;
    -webkit-animation: scrl 2s infinite;
    animation: scrl 2s infinite;
    opacity: 0;
    -webkit-animation-delay: .30s;
    animation-delay: .30s;
  }

  .arrowDown__arrow3 {
    margin: 12px;
    border-right: 7px solid #F85C37;
    border-bottom: 7px solid #F85C37;
    position: absolute;
    top: -30px;
    left: -30px;
    padding: 10px;
    -webkit-animation: scrl 2s infinite;
    animation: scrl 2s infinite;
    opacity: 0;
  }
