@-webkit-keyframes scrl {
  0%, 100% {
    opacity: 0;
  }
  50% {
      opacity: 1;
  }
}
@keyframes scrl {
  0%, 100% {
    opacity: 0;
  }
  50% {
      opacity: 1;
  }
}
.animated_squares{
    position:absolute;
    top:0px;
    bottom:0px;
    left:0px;
    right:0px;
    width:100%;
}

.animated_squares .uc-circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.animated_squares .uc-circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background-color:#ffffff;
    opacity:0.2;
    -webkit-animation: animate-bg-squares 25s linear infinite;
            animation: animate-bg-squares 25s linear infinite;
