    cursor: pointer;
    height: 300px;
    overflow: hidden;
    background: #000;
    margin: 0px 0px;
    width: 100%;
    border-radius: 10px 10px 0 0;
}

.galery__img {
    opacity: 0.7;
    transition: opacity 0.35s;
}

.galery__item:hover .galery__img {
    opacity: 0.4;
}

.caption {
    height: 100%;
    position: absolute;
    top: 0;
    width: 100%;
    left: 0;
    border-radius: 10px 10px 0 0;
}

.caption::before,
.caption::after {
    position: absolute;
    top: 30px;
    right: 30px;
    bottom: 30px;
    left: 30px;
    content: '';
    opacity: 0;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
}

.caption::before {
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    -webkit-transform: scale(0,1);
    transform: scale(0,1);
}

.caption::after {
    border-right: 1px solid #fff;
    border-left: 1px solid #fff;
    -webkit-transform: scale(1,0);
    transform: scale(1,0);
}

.caption__title {
    font-weight: 300;
    color: #fff;
    text-transform: uppercase;
    text-align: center;
    padding-top: 85px;
    transition: -webkit-transform 0.35s;
    transition: transform 0.35s;
    transition: transform 0.35s, -webkit-transform 0.35s;
    -webkit-transform: translate3d(0,-20px,0);
    transform: translate3d(0,-20px,0);
}

.caption__description {
    color: #fff;
    letter-spacing: 1px;
    font-size: 17px;
    padding: 20px 2.5em;
    opacity: 0;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
    -webkit-transform: translate3d(0,20px,0);
    transform: translate3d(0,20px,0);
}
.whiteSquare.galery__img{
    width: 100%;
}

.galery__item:hover .caption::before,
.galery__item:hover .caption::after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}

.galery__item:hover .caption__title,
.galery__item:hover .caption__description {
    opacity: 1;
    -webkit-transform: translate3d(0,-30px,0);
    transform: translate3d(0,-30px,0);
}

.caption__title span {
    font-weight: 800;
}
